import React, {Component} from "react";
import {OraButton, OraModal} from "eshop/components/OraCommonComponents";

class MultiCartRemovePopup extends Component {

    constructor(props) {
        super(props);
    }

    substituteElementName(text, substitution) {
        return {__html: text.replace(/\$/g, substitution)};
    }

    render() {
        return (
            <OraModal id={ this.props.id } showClose={true} narrow={true} fallback={ this.props.onClickRemove }>
                <div className="l-page-row u-margin-l">
                    <div className="l-row u-spacing">
                        <div className="l-col l-col-12">
                            <h2>{ this.props.title }</h2>
                        </div>
                    </div>
                    <div className="l-row u-spacing-xl">
                        <div className="l-col l-col-12" dangerouslySetInnerHTML={this.props.content && this.substituteElementName(this.props.content, this.props.textRepresentation)} />
                    </div>
                    <div className="l-row">

                        {/*smartphone view*/}
                        <div className="u-medium-hide u-large-hide ">
                            <div className="l-col l-col-small-12 l-col-medium-4 l-col-4 u-margin-s">
                                <OraButton onClick={this.props.onClickRemove} className="u-w-100" modal={true}>
                                    {this.props.confirm}
                                </OraButton>
                            </div>
                            <div className="l-col l-col-small-12 l-col-medium-4 l-col-4">
                                <OraButton className="u-w-100" modal={true} type="secondary">
                                    {this.props.decline}
                                </OraButton>
                            </div>
                        </div>

                        {/*desktop view*/}
                        <div className="u-small-hide">
                            <div className="l-col l-col-small-12 l-col-medium-4 l-col-4">
                                <OraButton className="u-w-100" modal={true} type="secondary">
                                    {this.props.decline}
                                </OraButton>
                            </div>
                            <div className="l-col l-col-small-12 l-col-medium-4 l-col-4">
                                <OraButton onClick={this.props.onClickRemove} className="u-w-100" modal={true}>
                                    {this.props.confirm}
                                </OraButton>
                            </div>
                        </div>

                    </div>
                </div>
            </OraModal>
        )
    }

}

MultiCartRemovePopup.defaultProps = {
    title: "Usunięcie elementu",
    content: "Usuwasz element $. Jesteś pewny?",
    confirm: "Zmień",
    decline: "Anuluj"
};

export default MultiCartRemovePopup;
